<template>
  <div class="a">
    <h1>Descargar Videos de Youtube</h1>
    <div class="flex">
      <input
        type="text"
        v-model="link"
        required
        placeholder="Ingresar link del video que quieres descargar"
        class="input"
      />
      <button class="boton" @click="verificar">Convertir</button>
    </div>
    <modal-descargas v-if="aside" :identidad="id" />
  </div>
</template>

<script>
import ModalDescargas from "./ModalDescargas.vue";
export default {
  name: "SectionMain",
  data() {
    return {
      link: "",
      id: "",
      aside: false,
    };
  },
  methods: {
     funcionAsideCrear() {
      this.aside = !this.aside
    },
    verificar() {
      if (this.link.length === 0) {
        alert("No has ingresado ningun link");
      } else if (this.link.includes("youtube.com/watch")) {
        this.id = this.link.substring(32, 43);
        this.aside = !this.aside;
      } else if (this.link.includes("youtu.be")){
        this.id = this.link.substring(17);
        this.aside = !this.aside;
      }else if(this.link.includes("youtube.com/shorts")){
        this.id = this.link.substring(31);
        this.aside = !this.aside;
      }
      else {
        alert("No es un link de Youtube");
      }
    },
   
  },
  components: { ModalDescargas },
};

</script>

<style>
.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.input {
  width: 500px;
  height: 40px;
  border: solid rgba(128, 128, 128, 0.322) 1px;
  border-radius: 8px;
  text-align: center;
}
.a{
  margin: 50px;
}
.contenedor{
  padding: 210px;
}

.boton {
  height: 45px;
  width: 100px;
  background: rgb(46, 232, 46);
  border: solid rgba(128, 128, 128, 0.103) 1px;
  margin: 10px;
  border-radius: 8px;
}

.boton:hover {
  background: rgb(49, 150, 49);
  cursor: pointer;
  transition: all 400ms ease;
}
</style>
