<template>
  <aside class="modal">

    <div class="title">
    <h2>{{ informacion.title }}</h2>
    <p>{{ informacion.author }}</p>
    <img :src="informacion.thumb" :alt="informacion.title">
    </div>


    <div class="grid">
        <botones-link v-for="(info, i) in links" :info="info" :key="i" />
    </div>
  </aside>
</template>

<script>
import BotonesLink from "./BotonesLink.vue";
export default {
  name: "ModalDescargas",
  mounted() {
    const options = {
      method: "GET",
      headers: {
        "X-RapidAPI-Key": "fda40cf90amsh420ab8f733023ebp135b73jsne7ab67a2b4be",
        "X-RapidAPI-Host": "youtube-video-download-info.p.rapidapi.com",
      },
    };

    fetch(
      `https://youtube-video-download-info.p.rapidapi.com/dl?id=${this.id}`,
      options
    )
      .then((response) => response.json())
      .then((json) => (this.informacion = json))
      .then((json) => json.link)
      .then((array) =>
        Object.keys(array).map((key) => {
          return array[key];
        })
      )
      .then(
        (array) =>
          (this.links = array.map((el) => {
            return {
              link: el[0],
              peso: el[1],
              calidad: el[3],
              tipo: el[4],
            };
          }))
      )
      .catch((err) => console.error(err));
  },
  props: ["identidad"],
  data() {
    return {
      id: this.identidad,
      informacion: [],
      links: [],
    };
  },
  components: {
    BotonesLink,
  },

};
</script>

<style>
.modal {
  background: #222;
  color: white;
  margin-top: 100px;
  justify-content: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 400px);
  justify-content: center;
}

.title{
  margin-bottom: 100px;
}

.close {
  color: #fff;
  cursor: pointer;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(-100%);
  font-size: 50px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
  z-index: 1020;
}

img{
  margin: 20px;
}

@media screen and (max-width: 1100px) {
  .modal{
    display: grid;
    justify-content: center;
  }
  .grid{
    grid-template-columns: repeat(auto-fill, 370px)
  }
}
</style>
